@import url(https://fonts.googleapis.com/css2?family=Squada+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pirata+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
}

:root {
  --ship-color: #8b949e;
  --sunk-color: #282c34;
  --bg-color: #607d8b;
  --board-color: #49cad7;
  --battleship-lg: linear-gradient(135deg, #1f1c2c 30%, #928dab 90%);
  --button-color: #212121;
  --hover-ship-color: greenyellow;
}

html {
  background-color: #607d8b;
  background-color: var(--bg-color);
}

.App {
  text-align: center;
}

.board .board-row {
  display: flex;
  flex-direction: row;
}

.gameboard {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.gameboard .board {
  margin: 0 20px;
  background-color: #49cad7;
}

div.board {
  border: 1px solid black;
}

button {
  margin-top: 15px;
  padding: 8px 20px;
  background-color: #212121;
  background-color: var(--button-color);
  color: white;
  border-radius: 4px;
  border: 0px;
  transition: -webkit-filter 100ms;
  transition: filter 100ms;
  transition: filter 100ms, -webkit-filter 100ms;
}

button:hover {
  -webkit-filter: brightness(1.2);
          filter: brightness(1.2);
}

@media only screen and (max-width: 950px) {
  .gameboard {
    flex-direction: column;
  }

  .gameboard .board {
    margin: 20px auto;
  }
}

.square.ship {
  background-color: var(--ship-color);
  border-left: 1px solid var(--ship-color);
  border-right: 1px solid var(--ship-color);
  border-bottom: 1px solid var(--ship-color);
  border-top: 1px solid var(--ship-color);
}

.ship.hidden {
  background-color: white;
}

.square.miss {
  background-color: grey;
}

.ship.hit {
  background-color: red;
}

.ship.sunk {
  background-color: var(--sunk-color);
  border: 1px solid var(--sunk-color);
}

.ship.top {
  border-top: 1px solid black;
}

.ship.bottom {
  border-bottom: 1px solid black;
}

.ship.right-edge {
  border-right: 1px solid black;
}

.ship.left-edge {
  border-left: 1px solid black;
}

.ship.vertical {
  border-left: 1px solid black;
  border-right: 1px solid black;
}

.no-click {
  pointer-events: none;
}

.square {
  width: 40px;
  height: 40px;
  border: 1px solid black;
}

.square.hover-ship {
  background-color: var(--hover-ship-color);
  border-left: 1px solid var(--hover-ship-color);
  border-right: 1px solid var(--hover-ship-color);
  border-bottom: 1px solid var(--hover-ship-color);
  border-top: 1px solid var(--hover-ship-color);
}

@media only screen and (max-width: 445px) {
  .square {
    width: 30px;
    height: 30px;
    border: 1px solid black;
  }

  circle {
    cx: 15;
    cy: 15;
    r: 10;
  }
}

.header > h1 > span {
  font-family: 'Squada One', cursive;
  font-size: 3em;
  background-image: var(--battleship-lg);
  -webkit-background-clip: text;
  color: transparent;
}

@media only screen and (max-width: 445px) {
  .header > h1 > span {
    font-size: 2em;
  }
}

.messages {
  font-family: 'Pirata One', cursive;
  margin: 0 auto 15px;
  font-size: x-large;
  width: 50%;
}

